.home {
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .hero {
    text-align: center;
    padding: 4rem 0;
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .hero h1 {
    font-size: 3.5rem;
    margin-bottom: 1rem;
    color: #64ffda;
  }
  
  .hero h2 {
    font-size: 2rem;
    color: #64ffda;
    margin-bottom: 1rem;
  }
  
  .hero p {
    font-size: 1.2rem;
    color: #8892b0;
    max-width: 600px;
    margin-bottom: 2rem;
  }
  
  .skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
  }
  
  .skill-category {
    background: #1a1a1a;
    padding: 2rem;
    border-radius: 8px;
    text-align: center;
  }
  
  .skill-category span {
    display: inline-block;
    background: #112240;
    color: #64ffda;
    padding: 0.5rem 1rem;
    margin: 0.5rem;
    border-radius: 4px;
  }